import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import ClientDocumentTypesTable from './Tables/ClientDocumentTypesTable';
import ClientDocumentTypesModal from './Modals/ClientDocumentTypesModal';
import ClientTypeSearch from '../../SearchComponents/ClientTypeSearch/ClientTypeSearch';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';

class ClientDocumentTypes extends React.Component {
    constructor(props, context) {
        super(props, context);
       
        this.state = {
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            filteredClientTypeData: [],
            filteredStatusData: [],
            value: '',
            show: false,
            showdelete: false,
            CLIENT_DOCUMENT_TYPE_ID: 0,
            CLIENT_DOCUMENT_TYPE_NAME: '',
            SET_CLIENT_TYPE_ID: 0,
            SET_SYS_STATUS_ID: 0,
            CREATEDBY:props.user.EMAIL,
            LASTMODIFIEDBY:props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            openclienttypesearch: false,
            openstatussearch: false,

            CLIENTTYPEDESCRIPTION: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',

            searchcolumns: [],

            searchdata: [],
            searchclienttypedata: [],
            searchstatusdata: [],

            searchFilteredClientTypeData: [],
            searchFilteredStatusData: [],

            searchclienttypevalue: '',
            searchstatusvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowClientTypeSearch = () => {
            this.setState({ openclienttypesearch: true });
        };

        this.handleShowStatusSearch = () => {
            this.setState({ openstatussearch: true });
        };

        this.handleHideClientTypeSearch = () => {
            this.setState({ openclienttypesearch: false });
        };

        this.handleHideStatusSearch = () => {
            this.setState({ openstatussearch: false });
        };

        this.populateData();
        this.populateTypes();
        this.populateStatus();

    }

   

    populateData() {
        const getdata = `
            query{
                ClientDocumentTypes{
                    CLIENT_DOCUMENT_TYPE_ID
                    CLIENT_DOCUMENT_TYPE_NAME                    
                    SET_CLIENT_TYPE_ID   
                    CLIENTTYPE{
                        CLIENT_TYPE_ID
                        CLIENT_TYPE_DESCRIPTION
                    }                    
                    SET_SYS_STATUS_ID
                    SYSTEMSTATUS{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.ClientDocumentTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Client Document Types Data');
                }
            })

    }

    populateTypes() {
        const getdata = `
            query {
                ClientTypes {
                    CLIENT_TYPE_ID,
                    CLIENT_TYPE_DESCRIPTION
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchclienttypedata: data.data.ClientTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Client Type Data');
                }
            })
    }

    populateStatus() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchstatusdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {
            const form = `
            mutation{
                addClientDocumentType(
                    CLIENT_DOCUMENT_TYPE_NAME:"${this.state.CLIENT_DOCUMENT_TYPE_NAME}",                
                    SET_CLIENT_TYPE_ID:${this.state.CLIENT_TYPE_ID},                                        
                    SET_SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.LASTMODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        CLIENT_DOCUMENT_TYPE_ID
                }
            }
            `;

            const formEdit = `
            mutation{
                updateClientDocumentType(
                    CLIENT_DOCUMENT_TYPE_ID:${this.state.CLIENT_DOCUMENT_TYPE_ID},
                    CLIENT_DOCUMENT_TYPE_NAME:"${this.state.CLIENT_DOCUMENT_TYPE_NAME}",                
                    SET_CLIENT_TYPE_ID:${this.state.CLIENT_TYPE_ID},                                        
                    SET_SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.LASTMODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        CLIENT_DOCUMENT_TYPE_ID
                }
            }
            `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
        this.setState({
            validated: true
        })
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteClientDocumentType(
                CLIENT_DOCUMENT_TYPE_ID:${this.state.CLIENT_DOCUMENT_TYPE_ID}
                ){
                    CLIENT_DOCUMENT_TYPE_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseClientTypeSearch = (e) => {

        this.setState({
            CLIENT_TYPE_ID: e.CLIENT_TYPE_ID,
            CLIENTTYPEDESCRIPTION: e.CLIENT_TYPE_DESCRIPTION,
            openclienttypesearch: false,
        });

    };

    handleCloseStatusSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            openstatussearch: false,
        });

    };

    handleEditData = (e) => {
        this.setState({
            CLIENT_DOCUMENT_TYPE_ID: e.CLIENT_DOCUMENT_TYPE_ID,
            CLIENT_DOCUMENT_TYPE_NAME: e.CLIENT_DOCUMENT_TYPE_NAME,
            SET_CLIENT_TYPE_ID: e.SET_CLIENT_TYPE_ID,
            CLIENTTYPEDESCRIPTION: e.CLIENTTYPE.CLIENT_TYPE_DESCRIPTION,
            SET_SYS_STATUS_ID: e.SET_SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYSTEMSTATUS.SYS_STATUS_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            CLIENT_DOCUMENT_TYPE_ID: e.CLIENT_DOCUMENT_TYPE_ID,
            CLIENT_DOCUMENT_TYPE_NAME: e.CLIENT_DOCUMENT_TYPE_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            CLIENT_DOCUMENT_TYPE_NAME: '',
            SET_CLIENT_TYPE_ID: '',
            CLIENTTYPEDESCRIPTION: '',
            SET_SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            validated: false,
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.CLIENT_DOCUMENT_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.CLIENTTYPE.CLIENT_TYPE_DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.CLIENT_DOCUMENT_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.CLIENTTYPE.CLIENT_TYPE_DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchClientTypeFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchclienttypedata
        let filteredClientTypeData = this.state.searchFilteredClientTypeData
        this.setState({ searchclienttypevalue: value });

        if (value.length) {
            filteredClientTypeData = data.filter(item => {
                let startsWithCondition =
                    item.CLIENT_TYPE_DESCRIPTION.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.CLIENT_TYPE_DESCRIPTION.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredClientTypeData })
        }
    }

    handleSearchStatusFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchstatusdata
        let filteredStatusData = this.state.searchFilteredStatusData
        this.setState({ searchstatusvalue: value });

        if (value.length) {
            filteredStatusData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredStatusData })
        }
    }

    render() {

        const { data, columns, value, filteredData, filteredClientTypeData, filteredStatusData, searchclienttypevalue, searchstatusvalue, searchstatusdata, searchclienttypedata } = this.state

        return (
            <>
                <Helmet><title>Client Document Types</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Client Document Types"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Client Document Types</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <ClientTypeSearch
                                        handleSearchClientType={this.handleCloseClientTypeSearch}
                                        opensearch={this.state.openclienttypesearch}
                                        handleHideSearch={this.handleHideClientTypeSearch}
                                        value={searchclienttypevalue}
                                        data={searchclienttypedata}
                                        handleFilter={this.handleSearchClientTypeFilter}
                                        filteredData={filteredClientTypeData}
                                    />

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseStatusSearch}
                                        opensearch={this.state.openstatussearch}
                                        handleHideSearch={this.handleHideStatusSearch}
                                        value={searchstatusvalue}
                                        data={searchstatusdata}
                                        handleFilter={this.handleSearchStatusFilter}
                                        filteredData={filteredStatusData}
                                    />

                                    <ClientDocumentTypesTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <ClientDocumentTypesModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowClientTypeSearch={this.handleShowClientTypeSearch}
                                        handleShowStatusSearch={this.handleShowStatusSearch}
                                        ClientDocumentTypeName={this.state.CLIENT_DOCUMENT_TYPE_NAME}
                                        changeClientDocumentTypeName={e => this.setState({ CLIENT_DOCUMENT_TYPE_NAME: e.target.value })}
                                        ClientTypeDescription={this.state.CLIENTTYPEDESCRIPTION}
                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(ClientDocumentTypes));