import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';

import { Search } from 'react-feather';

class ClientDocumentTypesModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                // size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Client Document Types
                        </Modal.Title>
                    </Modal.Header>

                    <Form noValidate validated={this.props.validated} onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>

                            <Form.Group controlId="ClientDocumentTypeName">
                                <Form.Label>Client Document Type Name</Form.Label>
                                <Form.Control type="text"
                               
                                placeholder="Enter Client Document Type Name"
                                value={this.props.ClientDocumentTypeName}
                                onChange={this.props.changeClientDocumentTypeName} required/>
                               <Form.Control.Feedback type="invalid">
                                    Please enter  Client Document.
                                 </Form.Control.Feedback>
                            </Form.Group>

                            <Form.Group controlId="ClientType">
                                <Form.Label>Client Type</Form.Label>
                                <InputGroup>
                                   
                                    <Form.Control
                                        type="text"
                                       
                                        value={this.props.ClientTypeDescription}
                                       
                                        placeholder="Search Client Type"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowClientTypeSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                    <Form.Control.Feedback type="invalid">
                                    Please select Client Type.
                                 </Form.Control.Feedback>
                                </InputGroup>
                                
                            </Form.Group>

                            <Form.Group controlId="SystemStatusType">
                                <Form.Label>System Status Type</Form.Label>
                                <InputGroup>
                                   
                                    <Form.Control
                                        type="text"
                                        name="SystemStatus"
                                        value={this.props.SystemStatusTypeDescription}
                                        
                                        placeholder="Search System Status Type"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowStatusSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                    <Form.Control.Feedback type="invalid">
                                    Please select Status.
                                 </Form.Control.Feedback>
                                </InputGroup>
                              
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Client Document Type
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.ClientDocumentTypeName} onChange={this.props.changeClientDocumentTypeName} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default ClientDocumentTypesModal;